export function getForm(httpClient: HttpClient): GetFormSignature {
  return (eventId: string) =>
    universalGetForm(
      eventId,
      // @ts-ignore
      { httpClient }
    );
}

interface GetFormSignature {
  /**
   * Retrieves an event registration form.
   * @param - Event ID to which the form belongs.
   * @param - Optional fields.
   * @returns Event form.
   * Form is visible to site visitors.
   */
  (eventId: string): Promise<
    NonNullablePaths<
      Form,
      | `controls`
      | `controls.${number}.type`
      | `controls.${number}.system`
      | `controls.${number}.name`
      | `controls.${number}.label`
      | `controls.${number}.orderIndex`
      | `controls.${number}._id`
      | `messages.rsvp.rsvpYesOption`
      | `messages.rsvp.rsvpNoOption`
      | `messages.rsvp.positiveMessages.title`
      | `messages.rsvp.positiveMessages.confirmation.title`
      | `messages.rsvp.positiveMessages.confirmation.message`
      | `messages.rsvp.positiveMessages.confirmation.addToCalendarActionLabel`
      | `messages.rsvp.positiveMessages.confirmation.shareActionLabel`
      | `messages.rsvp.negativeMessages.title`
      | `messages.rsvp.negativeMessages.confirmation.title`
      | `messages.rsvp.negativeMessages.confirmation.shareActionLabel`
      | `messages.rsvp.submitActionLabel`
      | `messages.checkout.title`
      | `messages.checkout.submitActionLabel`
      | `messages.checkout.confirmation.title`
      | `messages.checkout.confirmation.message`
      | `messages.checkout.confirmation.downloadTicketsLabel`
      | `messages.checkout.confirmation.addToCalendarLabel`
      | `messages.checkout.confirmation.shareEventLabel`
      | `messages.registrationClosed.message`
      | `messages.registrationClosed.exploreEventsActionLabel`
      | `messages.ticketsUnavailable.message`
      | `messages.ticketsUnavailable.exploreEventsActionLabel`,
      6
    >
  >;
}

